import path from 'path';
//PHYSICS.extend.ThirdPersonModule = require('./ThirdPersonControls').ThirdPersonModule

console.log(PHYSICS)

const sphere = new WHS.Sphere({ // Create sphere comonent.
  geometry: {
    radius: 5,
    widthSegments: 32,
    heightSegments: 32
  },

  modules: [
    new PHYSICS.SphereModule({
      mass: 1,
      restitution: 1
    })
  ],

   material: {
    color: 0xffffff,
    kind: 'lambert'
  },

  position: new THREE.Vector3(0, 0, 0)
});

const app = new WHS.App([
  new WHS.ElementModule({
    container: document.getElementById('app')
  }),
  new WHS.DefineModule('camera', new WHS.PerspectiveCamera({
    position: new THREE.Vector3(0, 6, 18),
    far: 10000
  })),
  new WHS.SceneModule(),
  new WHS.RenderingModule({
    bgColor: 0x162129,

    renderer: {
      antialias: true,
      shadowmap: {
        type: THREE.PCFSoftShadowMap
      }
    }
  }, {shadow: true}),
  new PHYSICS.WorldModule({
    gravity: new THREE.Vector3(0, 0, 0),
    ammo: 'https://rawgit.com/WhitestormJS/physics-module-ammonext/master/vendor/ammo.js'
  }),
  new PHYSICS.FirstPersonModule(sphere, {
    speed: 500,
    ypos: 20,
  }),
  new WHS.ResizeModule()
]);

sphere.addTo(app);

// Plane
new WHS.Plane({
  geometry: {
    width: 1000,
    height: 1000
  },

  material: new THREE.MeshPhongMaterial({color: 0x447F8B}),

  rotation: {
    x: -Math.PI / 2
  }
}).addTo(app);

// Lights
new WHS.PointLight({
  light: {
    intensity: 0.5,
    distance: 100
  },

  shadow: {
    fov: 90
  },

  position: new THREE.Vector3(0, 10, 10)
}).addTo(app);

new WHS.AmbientLight({
  light: {
    intensity: 0.4
  }
}).addTo(app);

for (let i = 0; i < 10; i++) {
  const height = 10 + Math.random() * 90;

  new WHS.Box({
    geometry: {
      width: 10 + Math.random() * 90,
      height,
      depth: 10 + Math.random() * 90
    },

    modules: [
      new PHYSICS.SphereModule({
        mass: 0,
        restitution: 1
      })
    ],

    material: new THREE.MeshPhongMaterial({color: '#ffcc12'}),

    position: {
      x: Math.random() * 1000 - 500,
      y: height / 2,
      z: Math.random() * 1000 - 500
    }
  }).addTo(app);
}


// Start the app
app.start();